body {
  margin: 0;
  width: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#ffffff {} }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

img,
video {
  height: auto;
  max-width: 100%; }

.container {
  display: grid;
  grid-template-columns: 4fr 1fr;

  .canvas {
    display: grid;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color:#252525 {}
    color: #252525;
    padding: 30px;

    .label {
      width: 330px;
      margin: auto;
      display: grid;
      grid-template-columns: 1fr;
      grid-gap: 10px;

      .content {
        padding: 15px 20px;
        background: #ffffff;
        color: #000000;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;

        .day {
          font-size: 48px;
          text-align: center;
          font-family: SF-Pro-Display-Ultralight;
          margin-bottom: 20px; }

        .date {
          font-size: 170px;
          line-height: 136px;
          text-align: center;
          font-family: SF-Pro-Display-Black; }

        .monthAndYear {
          font-size: 24px;
          text-align: center;
          margin-bottom: 40px;

          .month {
            font-family: SF-Pro-Display-Black; }

          .year {
            font-family: SF-Pro-Display-Regular; } }

        .time {
          display: grid;
          grid-template-columns: auto 1fr;
          grid-gap: 10px;
          align-items: center;
          font-size: 32px;
          font-family: SF-Pro-Display-Light;
          margin-bottom: 20px;

          .timeIcon {
            width: 24px; } }

        .location {
          display: grid;
          grid-template-columns: auto 1fr;
          grid-gap: 10px;
          align-items: center;

          .locationIcon {
            width: 24px; }

          .locationDetails {

            .locationName {
              font-size: 18px;

              .host {
                font-family: SF-Pro-Display-Black; }

              .hall {
                font-family: SF-Pro-Display-Light; } }

            .locationAddress {
              font-size: 12px;
              font-family: SF-Pro-Display-Regular; } } } }

      .ornaments {
        position: relative;
        padding: 10px;
        background: #ffffff;
        color: #000000;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 10px;
        align-items: center;

        &::after {
          content: '';
          position: absolute;
          height: 32px;
          width: 32px;
          background-color: #ffffff;
          bottom: -16px;
          left: calc(50% - 16px);
          clip-path: polygon(0% 0%, 100% 100%, 0% 100%);
          transform: rotate(-45deg);
          border-radius: 0 0 0 0.25em; }

        .partner {
          font-size: 10px;
          font-family: SF-Pro-Display-Bold; }

        .partnerLogo {
          padding-top: 5px; } } } }


  .tools {
    display: grid;
    align-items: center;
    padding: 30px;
    background-color: #ffffff;
    font-family: system-ui;
    color: #252525;

    .time-presets-wrapper {
      display: grid;
      grid-template-columns: 1fr auto;
      align-items: center; } } }
